import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { GetCategorySubCategoryComponent } from './GetCategorySubCategory.component';
/* import "jquery-locationpicker/src/locationpicker.jquery"; */

export const routes = [
  { path: '', component: GetCategorySubCategoryComponent, pathMatch: 'full' }
];

@NgModule({
  declarations: [
    GetCategorySubCategoryComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    RouterModule.forChild(routes),
    
  ]
})
export default class GetCategorySubCategoryModule {
  static routes = routes;
}



